import { Inject, Injectable, Logger } from '@nestjs/common';
import { DeviceService } from '../../device/device.service';
import { IAnalyzeTransferBillResult, IBankService } from '../interfaces/bank-service.interface';
import { Withdrawal } from '../../../common/interfaces/withdrawal.interface';
import { ICaptureScreenOptions, IWebSocketService } from '@/common/modules/websocket/interface/websocket.interface';
import { WEBSOCKET_SERVICE } from '@/common/modules/websocket/constants/websocket';
import { sleep } from '@/common/utils/time';
import { existsSync, mkdirSync } from 'fs';
import { mkdir, readdir, readFile, stat } from 'fs/promises';
import { join, resolve } from 'path';
import { getMimeType } from '../constants/bill';
import { analyzeBill, ocrBillPro } from '@/modules/ocr/ocr';


/**
 * Base abstract class for all bank services
 * Provides common functionality and enforces interface implementation
 */
@Injectable()
export abstract class BaseBankService implements IBankService {
  protected readonly logger: Logger;
  protected abstract readonly BANK_CODE: string;
  protected abstract readonly BUNDLE_ID: string;

  // Static flags  trnh race condition khi nhiu class cng khi to
  protected readonly baseTransactionFolderPath = 'C:/EzpData';
  private static folderCreationInProgress = false;
  private static folderCreated = false;

  constructor(
    @Inject(WEBSOCKET_SERVICE)
    protected readonly wsService: IWebSocketService,
    protected readonly deviceService: DeviceService
  ) {
    this.logger = new Logger(this.constructor.name);
    BaseBankService.createTransactionFolder(this.baseTransactionFolderPath, this.logger);
  }

  /**
   * Kim tra v to base folder nu cha tn ti
   * S dng static method  trnh race condition khi nhiu class cng khi to
   */
  private static createTransactionFolder(folderPath: string, logger: Logger): void {
    // Nu  to ri th khng cn lm g
    if (BaseBankService.folderCreated) {
      return;
    }

    // Nu ang c process khc ang to folder, i mt cht ri check li
    if (BaseBankService.folderCreationInProgress) {
      // i ti a 100ms, check mi 10ms
      let attempts = 0;
      const maxAttempts = 10;
      while (BaseBankService.folderCreationInProgress && attempts < maxAttempts) {
        attempts++;
        // Synchronous wait (ch dng trong constructor, khng block event loop lu)
        const start = Date.now();
        while (Date.now() - start < 10) {
          // Busy wait 10ms
        }
      }
      // Sau khi i, check li xem  to cha
      if (BaseBankService.folderCreated || existsSync(folderPath)) {
        BaseBankService.folderCreated = true;
        return;
      }
    }

    try {
      // nh du ang to folder
      BaseBankService.folderCreationInProgress = true;

      // Kim tra li mt ln na (double-check pattern)
      if (!existsSync(folderPath)) {
        mkdirSync(folderPath, { recursive: true });
        logger.log(`Created base folder: ${folderPath}`);
      } else {
        logger.debug(`Base folder already exists: ${folderPath}`);
      }

      // nh du  to xong
      BaseBankService.folderCreated = true;
    } catch (error) {
      // Nu li l "folder already exists" th khng sao (c th xy ra trong race condition)
      if ((error as NodeJS.ErrnoException).code !== 'EEXIST') {
        logger.error(`Failed to create base folder ${folderPath}: ${error.message}`);
      } else {
        // Folder  tn ti (c th do process khc  to), nh du l  to
        BaseBankService.folderCreated = true;
        logger.debug(`Base folder already exists (created by another process): ${folderPath}`);
      }
    } finally {
      // Lun reset flag
      BaseBankService.folderCreationInProgress = false;
    }
  }

  getBankCode(): string {
    return this.BANK_CODE;
  }

  /**
   * Launch bank app on device with retry logic
   */
  async launchApp(deviceId: string, maxRetries: number = 3): Promise<void> {
    for (let attempt = 1; attempt <= maxRetries; attempt++) {
      try {
        if (attempt === 1) {
          this.logger.log(`Launching ${this.BANK_CODE} app on device ${deviceId}`);
        } else {
          this.logger.log(`Retrying launch ${this.BANK_CODE} app on device ${deviceId} (attempt ${attempt}/${maxRetries})`);
        }

        await this.wsService.launchApp(deviceId, this.BUNDLE_ID);

        if (attempt > 1) {
          this.logger.log(`${this.BANK_CODE} app launched successfully on device ${deviceId} after ${attempt} attempt(s)`);
        } else {
          this.logger.log(`${this.BANK_CODE} app launched successfully on device ${deviceId}`);
        }
        return;
      } catch (error) {
        const isTimeoutError = error instanceof Error && error.message.includes('timeout');

        // If not a timeout error, throw immediately (no retry for non-timeout errors)
        if (!isTimeoutError) {
          this.logger.error(`Error launching ${this.BANK_CODE} app on device ${deviceId} (attempt ${attempt}/${maxRetries}): ${error.message}`);
          throw error;
        }

        // Timeout error - retry if not last attempt
        if (attempt < maxRetries) {
          // Exponential backoff: 2s, 4s, 8s
          const delay = Math.pow(2, attempt) * 1000;
          this.logger.warn(
            `WebSocket timeout when launching ${this.BANK_CODE} app on device ${deviceId} (attempt ${attempt}/${maxRetries}). Retrying in ${delay}ms...`
          );
          await sleep(delay);
        } else {
          // Last attempt failed with timeout
          this.logger.error(`Error launching ${this.BANK_CODE} app on device ${deviceId} (attempt ${attempt}/${maxRetries}): ${error.message}`);
          throw error;
        }
      }
    }
  }

  async killApp(deviceId: string, maxRetries: number = 2): Promise<void> {
    for (let attempt = 1; attempt <= maxRetries; attempt++) {
      try {
        if (attempt === 1) {
          this.logger.log(`Killing ${this.BANK_CODE} app on device ${deviceId}`);
        } else {
          this.logger.log(`Retrying kill ${this.BANK_CODE} app on device ${deviceId} (attempt ${attempt}/${maxRetries})`);
        }

        await this.wsService.killApp(deviceId, this.BUNDLE_ID);
        // Wait for app to close
        await sleep(3000);

        if (attempt > 1) {
          this.logger.log(`${this.BANK_CODE} app killed successfully on device ${deviceId} after ${attempt} attempt(s)`);
        } else {
          this.logger.log(`${this.BANK_CODE} app killed successfully on device ${deviceId}`);
        }
        return;
      } catch (error) {
        const isTimeoutError = error instanceof Error && error.message.includes('timeout');

        // If not a timeout error, throw immediately (no retry for non-timeout errors)
        if (!isTimeoutError) {
          this.logger.error(`Error killing ${this.BANK_CODE} app on device ${deviceId} (attempt ${attempt}/${maxRetries}): ${error.message}`);
          throw error;
        }

        // Timeout error - retry if not last attempt
        if (attempt < maxRetries) {
          // Exponential backoff: 1s, 2s
          const delay = Math.pow(2, attempt - 1) * 1000;
          this.logger.warn(
            `WebSocket timeout when killing ${this.BANK_CODE} app on device ${deviceId} (attempt ${attempt}/${maxRetries}). Retrying in ${delay}ms...`
          );
          await sleep(delay);
        } else {
          // Last attempt failed with timeout
          this.logger.error(`Error killing ${this.BANK_CODE} app on device ${deviceId} (attempt ${attempt}/${maxRetries}): ${error.message}`);
          throw error;
        }
      }
    }
  }

  /**
   * Helper method for clicking with transition time and logging
   */
  async clickWithTransition(
    deviceId: string,
    x: number,
    y: number,
    transitionTime: number,
    log: string = ''
  ): Promise<void> {
    await this.wsService.click(deviceId, x, y, 0.2);
    if (log) {
      this.logger.log(log);
    }
    // Wait for screen transition
    await sleep(transitionTime);
  }

  async createBillFolder(folderPath: string): Promise<void> {
    const path = resolve(this.baseTransactionFolderPath, folderPath);

    await mkdir(path, { recursive: true });
  }

  /**
   * Capture screen of device
   */
  async captureScreen(deviceId: string, options: ICaptureScreenOptions) {
    this.logger.log(`Capturing screen on device ${deviceId} with folderPath: ${options.folderPath}`);

    const path = resolve(this.baseTransactionFolderPath, options.folderPath);

    await this.wsService.captureScreen(deviceId, {
      ...options,
      folderPath: path,
    });
  }

  /**
   * c folder v tm file nh u tin, sau  convert sang base64
   * @param folderPath ng dn n folder cha nh
   * @returns Base64 string ca nh hoc null nu khng tm thy
   */
  async getBillImage(folderPath: string): Promise<string | null> {
    try {
      const folder = join(this.baseTransactionFolderPath, folderPath);
      const files = await readdir(folder);

      // Tm file nh u tin
      const imageFile = files[0];

      if (!imageFile) {
        this.logger.warn(`No image file found in folder: ${folderPath}`);
        return null;
      }

      // c file nh
      const imagePath = join(folder, imageFile);
      const imageBuffer = await readFile(imagePath);

      // Convert sang base64
      const base64Image = imageBuffer.toString('base64');

      // Xc nh mime type t extension
      const ext = imageFile.toLowerCase().substring(imageFile.lastIndexOf('.'));
      const mimeType = getMimeType(ext);

      return `data:${mimeType};base64,${base64Image}`;
    } catch (error) {
      this.logger.error(`Error loading image from folder ${folderPath}: ${error.message}`);
      throw error;
    }
  }

  /**
   * c folder v tm file nh u tin, sau  convert sang base64
   * @param folderPath ng dn n folder cha nh
   * @returns Base64 string ca nh hoc null nu khng tm thy
   */
  async getBillImagePath(folderPath: string): Promise<string | null> {
    if (!folderPath) {
      this.logger.warn('Folder path is null or empty');
      return null;
    }
  
    try {
      const folder = join(this.baseTransactionFolderPath, folderPath);
      const files = await readdir(folder);
  
      if (files.length === 0) return null;
  
      const filesWithStat = await Promise.all(
        files.map(async file => {
          const fullPath = join(folder, file);
          const s = await stat(fullPath);
          return { fullPath, mtime: s.mtimeMs, size: s.size };
        }),
      );
  
      const validFiles = filesWithStat.filter(f => f.size > 0);
      if (validFiles.length === 0) {
        this.logger.warn(`No valid image file found in folder: ${folderPath}`);
        return null;
      }
  
      validFiles.sort((a, b) => b.mtime - a.mtime);
  
      return validFiles[0].fullPath;
    } catch (error) {
      this.logger.error(`Error loading image from folder ${folderPath}: ${error.message}`);
      return null;
    }
  }

  async analyzeTransferBill(withdrawal: Partial<Withdrawal>): Promise<IAnalyzeTransferBillResult> {
    const billImage = await this.getBillImagePath(withdrawal.withdrawalCode);

    try {
      // No image found
      if (!billImage) {
        this.logger.warn(`No bill image path ${withdrawal.withdrawalCode}`);

        return {
          rawPath: null,
          analyzedStatus: 'na',
        };
      }

      const ocrResult = await ocrBillPro(billImage);

      if (!ocrResult) {
        this.logger.warn(`No OCR result found for ${withdrawal.withdrawalCode}`);

        return {
          rawPath: billImage,
          analyzedStatus: 'na'
        }
      }

      const analyzedResult = analyzeBill(ocrResult.parsedText);

      return {
        rawPath: billImage,
        analyzedStatus: analyzedResult.analyzedStatus,
      };
    } catch (error) {
      this.logger.error(`Error analyzing transfer bill: ${error.message}`);

      return {
        rawPath: billImage,
        analyzedStatus: 'na'
      }
    }
  }

  /**
   * Abstract methods - must be implemented by each bank
   */
  abstract login(deviceId: string): Promise<void>;
  abstract executeInternalTransfer(withdrawal: Withdrawal, deviceId: string): Promise<void>;
  abstract executeExternalTransfer(withdrawal: Withdrawal, deviceId: string): Promise<void>;
  abstract executeTransfer(withdrawal: Withdrawal, deviceId: string): Promise<void>;
}

