import { config } from "@/common/constants/config";
import { ocrSpace } from "ocr-space-api-wrapper";
import { IAnalyzeTransferBillResult } from "../bank/interfaces/bank-service.interface";
import { Logger } from "@nestjs/common";


const logger = new Logger('OCRService');

/**
 * OCR mt nh t base64 string
 * @param base64Image Base64 string ca nh
 */
export const ocrBillFree = async (base64Image: string) => {
  try {
    // const language = OCR_BANK_LANGUAGES[withdrawal.bankCode];

    const result = await ocrSpace(base64Image,
      {
        apiKey: config.spaceocr.keyFree,
        language: 'eng',
        scale: true,
        OCREngine: '2',
        isTable: true,
      });
    return {
      parsedText: result.ParsedResults[0].ParsedText,
      processingTimeInMilliseconds: result.ProcessingTimeInMilliseconds,
    };
  } catch (error) {
    console.error(`Error processing OCR: ${error.message}`);
    throw error;
  }
}

/**
 * OCR mt nh t base64 string hoc path
 * @param base64Image Base64 string ca nh
 */
export const ocrBillPro = async (imageSource: string) => {
  try {
    const randomServer = Math.floor(Math.random() * 2) + 1;
    const datacenter = 
      randomServer === 1 ? config.spaceocr.datacenterPrimary : config.spaceocr.datacenterSecond;

    const result = await ocrSpace(imageSource, {
      apiKey: config.spaceocr.key,
      language: 'eng',
      scale: true,
      OCREngine: '2',
      isTable: true,
      ocrUrl: datacenter
    });
    
    return {
      parsedText: result.ParsedResults[0].ParsedText,
      processingTimeInMilliseconds: result.ProcessingTimeInMilliseconds,
    };
  } catch (error) {
    logger.error(`Error processing OCR: ${error.message}`);
    
    return null;
  }
}

const successValue = [
  'chuyn tin thnh cng',
  'funds transfer successful',
];

export const analyzeBill = (value: string): Omit<IAnalyzeTransferBillResult, 'rawPath'> => {
  const lowerValue = value.toLowerCase();
  if (successValue.some(v => lowerValue.includes(v))) {
    return {
      analyzedStatus: 'success'
    };
  }

  return {
    analyzedStatus: 'unknown',
  };
}